
@font-face {
  font-family: 'Enter the grid';
  src: url(https://3dmensional.github.io/3dmensional-website/static/fonts/Enter-The-Grid.891c61b.ttf);
}
@font-face {
  font-family: 'Walkway';
  src: url("https://3dmensional.github.io/3dmensional-website/static/fonts/Walkway Bold.767868a.ttf");
}
@font-face {
  font-family: 'Walkway-Bold';
  src: url("https://3dmensional.github.io/3dmensional-website/static/fonts/Walkway Black.93dbd2a.ttf");
}
* {
  list-style: none;
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Walkway', Arial, Helvetica, sans-serif;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
h1 {
  text-align: center;
  margin-bottom: 3em;
  font-family: 'Enter the grid', Arial, Helvetica, sans-serif;
  font-size: 2em;
}
h2 {
  font-family: 'Enter the grid', Arial, Helvetica, sans-serif;
  font-size: 1.4em;
  line-height: 1em;
  letter-spacing: 1px;
}
p {
  margin-bottom: 2em;
  line-height: 1.5em;
}
.d-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.d-grid {
  display: grid;
}
.fixed {
  position: fixed;
}
a.nav-link {
  color: #fff;
  text-decoration: none;
}
.content section:nth-child(odd) {
  color: white;
  background-color: black;
}
.content section:nth-child(even) {
  color: black;
}
section {
  min-height: 126vh;
  -webkit-transform: skewY(4deg);
          transform: skewY(4deg);
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  top: -7vh;
  padding: 0 4rem;
}
section > * {
    -webkit-transform: skewY(-4deg);
            transform: skewY(-4deg);
}
section .icon-next {
    position: absolute;
    bottom: 25vh;
}
nav {
  padding: 10px 1em;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 800;
  width: 100%;
}
nav figure {
    width: 70px;
}
nav figure svg {
      width: 100%;
}
nav figure svg path {
        -webkit-transition: all ease .3s;
        transition: all ease .3s;
}
nav figure svg.negative:hover path, nav figure svg.positive:hover path {
        -webkit-transition: all ease .3s;
        transition: all ease .3s;
}
nav figure svg.negative:hover .container, nav figure svg.positive:hover .container {
        fill: transparent;
}
nav figure svg.negative path {
        fill: white;
}
nav figure svg.negative .container {
        fill: transparent;
}
nav figure svg.negative:hover path {
        fill: #646464;
}
nav figure svg.positive path {
        fill: #000;
}
nav figure svg.positive .container {
        fill: transparent;
}
nav figure svg.positive:hover path {
        fill: white;
}
nav .menu {
    max-width: 25em;
    margin-right: 1em;
}
nav .menu ul {
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -ms-flex-line-pack: center;
          align-content: center;
      font-size: 1em;
}
nav .menu ul li {
        padding: 1em;
        position: relative;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
}
nav .menu ul li a {
          font: 1.2em 'Walkway-Bold', Arial, Helvetica, sans-serif;
}
nav .menu ul li::after {
          content: "";
          position: absolute;
          width: 0;
          bottom: 0;
          border-top: 3px solid #646464;
          -webkit-transition: width ease .3s;
          transition: width ease .3s;
}
nav .menu ul li:hover {
          cursor: pointer;
          -webkit-transition: all ease .3s;
          transition: all ease .3s;
}
nav .menu ul li:hover .nav-link {
            color: #646464;
            -webkit-transition: all ease .3s;
            transition: all ease .3s;
}
nav .menu ul li:hover::after {
            width: 70%;
            -webkit-transition: width ease .3s;
            transition: width ease .3s;
}
.banner h1 {
  position: absolute;
  left: 2em;
}
.about-us {
  min-height: 105vh;
  background: -webkit-gradient(linear, left top, right top, from(white), to(gainsboro));
  background: linear-gradient(to right, white, gainsboro);
  z-index: 1;
}
.about-us p {
    text-align: center;
}
.services {
  padding-top: 1em;
  padding-bottom: 1em;
  overflow: hidden;
}
.services .chevron-container {
    position: absolute;
    right: 0;
}
.services .chevron {
    height: 100vh;
}
.services .d-grid {
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
}
.services .show {
    opacity: 1;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}
.services h1 {
    justify-self: center;
    -ms-flex-item-align: end;
        align-self: flex-end;
    opacity: 0;
    grid-column: 2 / span 1;
}
.services p {
    text-align: center;
    margin: 0;
    opacity: 0;
    grid-column: 1 / span 1;
    grid-row: 2 / span 2;
}
.services .services-elements {
    padding: 0 7em;
    justify-self: center;
    -ms-flex-item-align: center;
        align-self: center;
    grid-column: 2 / span 1;
    grid-row: 3 / span 1;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
}
.services .services-elements span {
      background-color: rgba(255, 255, 255, 0.6);
      height: 50px;
      width: 50px;
      border-radius: 50%;
      margin: 1em 0;
      justify-self: center;
      -ms-flex-item-align: center;
          align-self: center;
}
.services .services-controller {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.services .services-controller svg {
      height: 2em;
      margin: .5em 0;
}
.services .services-controller .action-container {
      position: relative;
}
.services .services-controller .action-container a {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
}
.services .services-controller .action-container a:focus {
          outline: none;
}
.contact {
  background-image: url(https://3dmensional.github.io/3dmensional-website/static/img/contact-banner.56e0d45.jpg);
  background-size: cover;
  background-position: center center;
  min-height: 100vh;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  padding-top: 5em;
  padding-bottom: 5em;
  top: -8vh;
}
.contact > * {
    width: 50%;
}
.contact .form-container {
    -ms-flex-item-align: center;
        align-self: center;
    position: relative;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.contact .form-container form {
      position: absolute;
      max-width: 50%;
      height: 95%;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -ms-flex-pack: distribute;
          justify-content: space-around;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.contact .form-container form .input-group {
        width: 100%;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
}
.contact .form-container form .input-group label {
          display: inline-block;
          margin-right: 5px;
}
.contact .form-container form .input-group input,
        .contact .form-container form .input-group select,
        .contact .form-container form .input-group textarea {
          padding: 5px;
          border: 1px solid black;
          background: transparent;
}
.contact .form-container form .input-group input,
        .contact .form-container form .input-group select {
          width: 70%;
}
.contact .form-container form .input-group textarea {
          width: 100%;
          resize: none;
          margin-top: 10px;
}
.contact .form-container form .btn {
        background: transparent;
        border: 1px solid #000;
        padding: 1em 2.5em;
        -webkit-transition: all .3s ease;
        transition: all .3s ease;
}
.contact .form-container form .btn:hover {
          background: black;
          cursor: pointer;
          color: white;
          -webkit-transition: all .3s ease;
          transition: all .3s ease;
}
.contact .hexagon {
    width: 100%;
    -webkit-filter: drop-shadow(16px 16px 6px rgba(0, 0, 0, 0.3));
            filter: drop-shadow(16px 16px 6px rgba(0, 0, 0, 0.3));
    z-index: -2;
}
